import React, { Fragment } from "react";
import { Switch, Route } from "react-router";
import Home from "./Home/Home";
import SentimentAnalysis from "./Home/SentimentAnalysis";
import SimpleSentimentAnalysis from "./Home/SimpleSentimentAnalysis";

const Admin: React.FC = () => {
  return (
    <Fragment>
      <Switch>
        <Route path="/admin/dashboard">
          <Home />
        </Route>
        <Route path="/admin/twitter-sentiment-analysis">
          <SentimentAnalysis />
        </Route>
        <Route path="/admin/sentiment-analysis">
          <SimpleSentimentAnalysis />
        </Route>
      </Switch>
    </Fragment>
  );
};

export default Admin;
