import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
const Header: React.FC = () => {
  const activePage = useSelector((state: any) => state.activePage);

  React.useEffect(() => {
    var ele = document.getElementsByClassName("" + activePage)[0];
    var parent = document.getElementById("container");
    var node = parent?.querySelectorAll("li");
    node?.forEach((item, index) => {
      item.classList.remove("active");
    });

    if (ele !== undefined) {
      ele.classList.add("active");
    }
  }, [activePage]);

  return (
    <>
      <div
        className="sidebar"
        data-color="purple"
        data-background-color="white"
        data-image="../assets/img/sidebar-1.jpg"
      >
        <div className="logo">
          <a href="" className="simple-text logo-normal">
            <img
              src="/assets/img/logo3.png"
              style={{ height: "auto", width: "12em" }}
            />
          </a>
        </div>
        <div className="sidebar-wrapper" id="container">
          <ul className="nav">
            <Link to="/admin/dashboard">
              <li className="nav-item  dashboard " id="dashboard">
                <a className="nav-link">
                  <i className="material-icons">dashboard</i>
                  <p>Dashboard</p>
                </a>
              </li>
            </Link>
            <Link to="/admin/sentiment-analysis">
              <li className="nav-item  sentiment-analysis">
                <a className="nav-link">
                  <i className="material-icons">person</i>
                  <p>Sentiment Analysis</p>
                </a>
              </li>
            </Link>
            <Link to="/admin/twitter-sentiment-analysis">
              <li className="nav-item twitter-sentiment-analysis" id="test">
                <a className="nav-link" href="./tables.html">
                  <i className="material-icons">content_paste</i>
                  <p>Twitter Sentiment Analysis</p>
                </a>
              </li>
            </Link>
            
          </ul>
        </div>
      </div>
    </>
  );
};
export default Header;
