const initialState = {
  activePage: "dashboard",
};

export interface IActionBase {
  type: string;
  [prop: string]: any;
}

function rootReducers(state = initialState, action: IActionBase) {
  switch (action.type) {
    case "ACTIVE_PAGE":
      return {
        ...state,
        activePage: action.activePage,
      };

    default:
      return state;
  }
}

export default rootReducers;
