import React,{Fragment} from 'react';
import {Link} from 'react-router-dom';
const Footer: React.FC = () => {
  return(
		<>
			<footer className="site-footer p-0 pt-5">
				<div className="container">
					<div className="row">
						<div className="col-md-9">
							<div className="row">
								<div className="col-md-5">
									<h2 className="footer-heading mb-4">About Us</h2>
									<p style={{lineHeight:"1"}} >
										For us, it's not only about getting the job done right, on time, and on budget, but also about innovation – working closely with our clients, leveraging technologies to achieve solutions that exceed expectations and deliver exceptional user experiences every time. so don't put plans on hold we are your tech partner for project definition, application design, and technical development.
									</p>
								</div>
								<div className="col-md-3 ml-auto">
									<h2 className="footer-heading mb-4">Quick Links</h2>
									<ul className="list-unstyled">
										<li><Link to="/About" className="smoothscroll">About Us</Link></li>
										<li><Link to="/Services" className="smoothscroll">Services</Link></li>
										<li><Link to="/Contact" className="smoothscroll">Contact Us</Link></li>
									</ul>
								</div>
								<div className="col-md-3">
									<h2 className="footer-heading mb-4">Follow Us</h2>
									<a href="#" className="pl-0 pr-3"><span className="icon-facebook" /></a>
									<a href="#" className="pl-3 pr-3"><span className="icon-twitter" /></a>
									<a href="#" className="pl-3 pr-3"><span className="icon-instagram" /></a>
									<a href="#" className="pl-3 pr-3"><span className="icon-linkedin" /></a>
								</div>
							</div>
						</div>
						<div className="col-md-3">
							<h2 className="footer-heading mb-4">Subscribe Newsletter</h2>
							<form action="#" method="post" className="footer-subscribe">
								<div className="input-group mb-3">
									<input type="text" className="form-control border-secondary text-white bg-transparent" placeholder="Enter Email" aria-label="Enter Email" aria-describedby="button-addon2" />
									<div className="input-group-append">
										<button className="btn btn-primary text-black" type="button" id="button-addon2">Send</button>
									</div>
								</div>
							</form>
						</div>
					</div>
					<div className="row pt-2 mt-0 text-center">
						<div className="col-md-12">
							<div className="border-top pt-5">
								<p>
									{/* Link back to Colorlib can't be removed. Template is licensed under CC BY 3.0. */}
									Copyright © 2020 All rights reserved By Aimachinelab 
									{/* Link back to Colorlib can't be removed. Template is licensed under CC BY 3.0. */}
								</p>
							</div>
						</div>
					</div>
				</div>
			</footer>
		
		</>
	);
}
export default Footer;