import React, { Fragment, useState, ChangeEvent, FormEvent } from "react";
import Helper from "../../../common/helpers/Helper";
import { format } from "path";

const Login: React.FC = () => {
  const [formState, setFormState] = useState({
    username: { error: "", value: "" },
    password: { error: "", value: "" },
  });

  function handleOnchange(e: ChangeEvent<HTMLInputElement>): void {
    setFormState({
      ...formState,
      [e.target.name]: { error: "", value: e.target.value },
    });
  }

  async function submit(e: FormEvent<HTMLFormElement>): Promise<boolean> {
    e.preventDefault();
    if (isFormInvalid()) {
      Helper.toaster("Fill Out the form");
      return false;
    }
    let username = formState.username.value;
    let password = formState.password.value;
    console.log(username);
    console.log(password);
    if (username === "guest" && password === "password") {
      Helper.toaster("You have succesfully Logged in", "success");
      localStorage.setItem("username", "guest");
      //window.location.reload();
      window.location.replace( window.location.origin+'/admin/dashboard' );
      
    } else {
      Helper.toaster("Authentication is incorrect", "error");
    }

    return true;
  }

  function isFormInvalid() {
    return (
      formState.username.error ||
      formState.password.error ||
      !formState.username.value ||
      !formState.password.value
    );
  }

  return (
    <>
      <div className="col-12 d-flex justify-content-center pt-5">
        <div className="col-xl-5 col-lg-5 col-md-7 col-11">
          <div className="card card-profile">
            <div className="tt">
              <a href="#">
                <i className="fa fa-user fa-5x"></i>
              </a>
            </div>
            <form onSubmit={submit}>
              <div className="card-body">
                <div className="col-12 p-0">
                  <div className="form-group bmd-form-group text-left">
                    <label className="bmd-label-floating">Username</label>
                    <input
                      type="text"
                      name="username"
                      value={formState.username.value}
                      onChange={handleOnchange}
                      className="form-control pl-3"
                    />
                  </div>
                </div>
                <div className="col-12 p-0">
                  <div className="form-group bmd-form-group text-left">
                    <label className="bmd-label-floating">Passord</label>
                    <input
                      type="text"
                      name="password"
                      value={formState.password.value}
                      onChange={handleOnchange}
                      className="form-control pl-3"
                    />
                  </div>
                </div>

                <button type="submit" className="btn btn-primary btn-round">
                  Login
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
