import React, { Fragment, useState, ChangeEvent } from "react";
import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import ReactSpeedometer from "react-d3-speedometer";
import { useDispatch } from "react-redux";
import axios from "axios";

const SimpleSentimentAnalysis: React.FC = () => {
  const dispatch = useDispatch();
  const [sentiment, setSentiment] = useState({
    value: 0,
  });
  const [input, setInput] = useState("");

  React.useEffect(() => {
    dispatch({ type: "ACTIVE_PAGE", activePage: "sentiment-analysis" });
  }, []);

  function handleChange(e: any) {
    setInput(e.target.value);
  }

  async function checkSentiment(e: any) {
    e.preventDefault();

    /* const min = 0;
    const max = 1;
    const rand = min + Math.random() * (max - min);
    console.log(rand);
    setSentiment({ value: rand });*/

    try {
      axios
        .get("http://151.106.26.155:8000/sentiment", {
          params: {
            sentiment: input,
          },
        })
        .then(function (response) {
          console.log(response.data);
          setSentiment({ value: response.data.score });
        });
    } catch (e) {
      console.log(e);
    }
  }

  return (
    <>
      {<Header />}
      <div className="main-panel">
        {/* Navbar */}
        <nav className="navbar navbar-expand-lg navbar-transparent navbar-absolute fixed-top ">
          <div className="container-fluid">
            <div className="navbar-wrapper">
              <a className="navbar-brand" href="javascript:;">
                Dashboard
              </a>
            </div>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              aria-controls="navigation-index"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="sr-only">Toggle navigation</span>
              <span className="navbar-toggler-icon icon-bar" />
              <span className="navbar-toggler-icon icon-bar" />
              <span className="navbar-toggler-icon icon-bar" />
            </button>
            <div className="collapse navbar-collapse justify-content-end">
              <form className="navbar-form">
                <div className="input-group no-border">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Search..."
                  />
                  <button
                    type="submit"
                    className="btn btn-white btn-round btn-just-icon"
                  >
                    <i className="material-icons">search</i>
                    <div className="ripple-container" />
                  </button>
                </div>
              </form>
              <ul className="navbar-nav">
                <li className="nav-item">
                  <a className="nav-link" href="javascript:;">
                    <i className="material-icons">dashboard</i>
                    <p className="d-lg-none d-md-block">Stats</p>
                  </a>
                </li>
                <li className="nav-item dropdown">
                  <a
                    className="nav-link"
                    href="http://example.com"
                    id="navbarDropdownMenuLink"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i className="material-icons">notifications</i>
                    <span className="notification">5</span>
                    <p className="d-lg-none d-md-block">Some Actions</p>
                  </a>
                  <div
                    className="dropdown-menu dropdown-menu-right"
                    aria-labelledby="navbarDropdownMenuLink"
                  >
                    <a className="dropdown-item" href="#">
                      Mike John responded to your email
                    </a>
                    <a className="dropdown-item" href="#">
                      You have 5 new tasks
                    </a>
                    <a className="dropdown-item" href="#">
                      You're now friend with Andrew
                    </a>
                    <a className="dropdown-item" href="#">
                      Another Notification
                    </a>
                    <a className="dropdown-item" href="#">
                      Another One
                    </a>
                  </div>
                </li>
                <li className="nav-item dropdown">
                  <a
                    className="nav-link"
                    href="javascript:;"
                    id="navbarDropdownProfile"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i className="material-icons">person</i>
                    <p className="d-lg-none d-md-block">Account</p>
                  </a>
                  <div
                    className="dropdown-menu dropdown-menu-right"
                    aria-labelledby="navbarDropdownProfile"
                  >
                    <a className="dropdown-item" href="#">
                      Profile
                    </a>
                    <a className="dropdown-item" href="#">
                      Settings
                    </a>
                    <div className="dropdown-divider" />
                    <a className="dropdown-item" href="#">
                      Log out
                    </a>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        {/* End Navbar */}
        <div className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-7">
                <div className="card">
                  <div className="card-header card-header-primary">
                    <h4 className="card-title">Sentiment Analysis</h4>
                  </div>
                  <div className="card-body">
                    <form>
                      <div className="row">
                        <div className="col-md-12">
                          <div className="form-group">
                            <div className="form-group">
                              <label className="bmd-label-floating">
                                {" "}
                                Please Type If You want to know sentiment
                              </label>
                              <textarea
                                className="form-control pl-3"
                                rows={8}
                                value={input}
                                onChange={handleChange}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                      <button
                        type="submit"
                        className="btn btn-primary pull-right"
                        onClick={checkSentiment}
                      >
                        SUBMIT
                      </button>
                      <div className="clearfix" />
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-md-5">
                <div className="card card-profile">
                  <div className="card-body">
                    <ReactSpeedometer
                      minValue={0}
                      maxValue={1}
                      value={sentiment.value}
                      height={200}
                      //needleTransition={"easeQuadInOut"}
                      needleTransitionDuration={3333}
                      needleTransition="easeElastic"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {<Footer />}
      </div>
    </>
  );
};

export default SimpleSentimentAnalysis;
