import React, { Fragment, useState, ChangeEvent } from "react";
import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import ReactSpeedometer from "react-d3-speedometer";
import "../Home/sentiment_analysis.css";
import Chart from "react-apexcharts";
import axios from "axios";
import { useDispatch } from "react-redux";

const SentimentAnalysis: React.FC = () => {
  const dispatch = useDispatch();
  const [showLoading, setShowLoading] = useState(false);
  const [chartOption, setChartOption] = useState({
    options: {
      colors: ["#F7464A", "#46BFBD", "#FDB45C"],
      labels: ["Negative", "Positive", "Neutral"],
      plotOptions: {
        pie: {
          donut: {
            labels: {
              show: true,
            },
          },
        },
      },
    },
  });

  const [series, setSerieas] = useState({
    series: [44, 55, 51],
  });
  const [input, setInput] = useState({
    input: "",
  });
  const [submitted, setSubmitted] = useState(false);
  const [tweets, setTweets] = useState([
    {
      label: "",
      score: 0,
      text: "",
      username: "",
    },
  ]);
  const [description, setDescription] = useState({
    detail: "No Data Has been Found",
  });

  React.useEffect(() => {
    console.log("twiiter sentient analysis runs");
    dispatch({ type: "ACTIVE_PAGE", activePage: "twitter-sentiment-analysis" });
  }, []);

  function handleChange(e: ChangeEvent<HTMLInputElement>): void {
    setInput({ input: e.target.value });
  }

  async function submitHandler() {
    setShowLoading(true);
    setSubmitted(false);
    try {
      axios
        .get("http://151.106.26.155:8000/analyzetweets", {
          params: {
            text: input.input,
          },
        })
        .then(function (response) {
          console.log(response.data);
          setTweets(response.data.tweets);
          setShowLoading(false);
          setSerieas({
            series: [
              response.data.negative,
              response.data.positive,
              response.data.neutral,
            ],
          });
          setSubmitted(true);
        });
    } catch (e) {
      console.log(e);
    }
    try {
      var url =
        "https://en.wikipedia.org/w/api.php?format=json&action=query&prop=extracts&exintro&explaintext&redirects=1&titles=" +
        input.input;
      axios.get(url).then(function (response) {
        console.log(response);
        var pageId = [];
        var data = response.data.query.pages;
        for (var id in data) {
          pageId.push(id);
        }
        console.log(pageId[0]);
        console.log(data[pageId[0]].extract);
        setDescription({ detail: data[pageId[0]].extract });
      });
    } catch (e) {
      console.log(e);
    }
    ///const fr = await Helper.post({ hashtag: hashTag }, "http://127.0.0.1:8000");
  }

  const userElements: (JSX.Element | null)[] = tweets.map((tweet, i) => {
    if (tweet.label === "" && submitted === false) {
      return null;
    }
    var color = "#46BFBD";

    if (tweet.label == "Neutral") {
      color = "#FDB45C";
    }
    if (tweet.label == "Negative") {
      color = "#F7464A";
    }
    return (
      <div key={i} className="row tweets">
        <h2>@{tweet.username}</h2>
        <p>{tweet.text}</p>
        <h3 style={{ color: color }}>Predicted Sentiment - {tweet.label}</h3>
      </div>
    );
  });

  return (
    <>
      {<Header />}
      <div className="main-panel">
        {/* Navbar */}
        <nav className="navbar navbar-expand-lg navbar-transparent navbar-absolute fixed-top ">
          <div className="container-fluid">
            <div className="navbar-wrapper">
              <a className="navbar-brand" href="javascript:;">
                Dashboard
              </a>
            </div>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              aria-controls="navigation-index"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="sr-only">Toggle navigation</span>
              <span className="navbar-toggler-icon icon-bar" />
              <span className="navbar-toggler-icon icon-bar" />
              <span className="navbar-toggler-icon icon-bar" />
            </button>
            <div className="collapse navbar-collapse justify-content-end">
              <form className="navbar-form">
                <div className="input-group no-border">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Search..."
                  />
                  <button
                    type="submit"
                    className="btn btn-white btn-round btn-just-icon"
                  >
                    <i className="material-icons">search</i>
                    <div className="ripple-container" />
                  </button>
                </div>
              </form>
              <ul className="navbar-nav">
                <li className="nav-item">
                  <a className="nav-link" href="javascript:;">
                    <i className="material-icons">dashboard</i>
                    <p className="d-lg-none d-md-block">Stats</p>
                  </a>
                </li>
                <li className="nav-item dropdown">
                  <a
                    className="nav-link"
                    href="http://example.com"
                    id="navbarDropdownMenuLink"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i className="material-icons">notifications</i>
                    <span className="notification">5</span>
                    <p className="d-lg-none d-md-block">Some Actions</p>
                  </a>
                  <div
                    className="dropdown-menu dropdown-menu-right"
                    aria-labelledby="navbarDropdownMenuLink"
                  >
                    <a className="dropdown-item" href="#">
                      Mike John responded to your email
                    </a>
                    <a className="dropdown-item" href="#">
                      You have 5 new tasks
                    </a>
                    <a className="dropdown-item" href="#">
                      You're now friend with Andrew
                    </a>
                    <a className="dropdown-item" href="#">
                      Another Notification
                    </a>
                    <a className="dropdown-item" href="#">
                      Another One
                    </a>
                  </div>
                </li>
                <li className="nav-item dropdown">
                  <a
                    className="nav-link"
                    href="javascript:;"
                    id="navbarDropdownProfile"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i className="material-icons">person</i>
                    <p className="d-lg-none d-md-block">Account</p>
                  </a>
                  <div
                    className="dropdown-menu dropdown-menu-right"
                    aria-labelledby="navbarDropdownProfile"
                  >
                    <a className="dropdown-item" href="#">
                      Profile
                    </a>
                    <a className="dropdown-item" href="#">
                      Settings
                    </a>
                    <div className="dropdown-divider" />
                    <a className="dropdown-item" href="#">
                      Log out
                    </a>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        {/* End Navbar */}
        <div className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-12">
                <div className="card">
                  <div className="card-header card-header-primary">
                    <h4 className="card-title">Twitter Sentiment Analysis</h4>
                  </div>
                  <div className="card-body">
                    <form>
                      <div className="row">
                        <div className="col-md-12">
                          <div className="form-group">
                            <div className="form-group">
                              <div className="input-group mb-3">
                                <div className="input-group-prepend">
                                  <span
                                    className="input-group-text hashtag-icon"
                                    id="basic-addon1"
                                  >
                                    #
                                  </span>
                                </div>
                                <input
                                  type="text"
                                  className=" hashtag"
                                  id="basic-url"
                                  aria-describedby="basic-addon3"
                                  value={input.input}
                                  onChange={handleChange}
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-sm-12">
                          <div className="text-center">
                            <button
                              className="btn text-center btn-primary submit"
                              type="button"
                              onClick={submitHandler}
                            >
                              Analyze
                            </button>
                          </div>
                        </div>
                      </div>
                      <div className="clearfix" />
                    </form>
                    <div className="row mt-5">
                      <div className="col-12">
                        {showLoading === true ? (
                          <div className="text-center">
                            <img
                              src={require("../../../loading_bar.gif")}
                              width="50"
                            />
                            <h2 className="progressheader">Please Wait</h2>
                          </div>
                        ) : null}
                        {submitted ? (
                          <div className="row bg-white tweets">
                            <div className="col-sm-3">
                              <Chart
                                options={chartOption.options}
                                series={series.series}
                                type="donut"
                                width="420"
                              />
                            </div>
                            <div
                              className="offset-sm-3 col-sm-6 p-3"
                              style={{ overflow: "scroll", height: "300px" }}
                            >
                              <h3 className="heading_desc">
                                {description.detail}
                              </h3>
                              <br />
                              <br />
                            </div>
                          </div>
                        ) : null}

                        {submitted ? userElements : null}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {<Footer />}
      </div>
    </>
  );
};

export default SentimentAnalysis;
